import { Battle, Entity, Skill } from '..';
import { SHEvent } from '../../utils';

/**
 * 施法状态管理器, 后续所有实体应该都是用这个, 得好好写一写...
 * 状态机不行, 应该多加标志位和判断器
 */
export class EntityController {
  /** 下一个释放的技能 */
  nextCast?: Skill;

  /** 当前释放的技能, 读条剩余的时间 */
  castRelease: number = 0;
  /** 当前释放的技能, 读条全部的时间 */
  castMax: number = 0;
  /** 当前正在读条的技能 */
  casting?: Skill;

  /** 公共cd */
  commonCD: number = 1.5;
  /** 公共cd剩余 */
  commonCDRelease: number = 0;
  /** 剩余控制时间, 眩晕需要同步添加buff和这里的时间 */
  stuckRelease: number = 0;

  /** 当前施法状态显示文字 */
  public get castingLabel() {
    if (this.casting) {
      if (this.casting.type === 'duration') {
        return `${this.casting.name}(${this.castRelease.toFixed(2)}s/${
          this.castMax
        }s)`;
      }

      return `${this.casting.name}(${(this.castMax - this.castRelease).toFixed(
        2
      )}s/${this.castMax}s)`;
    } else {
      if (this.commonCDRelease > 0 && globalSetting.showCommonCD) {
        return `公共cd:${this.commonCDRelease.toFixed(2)}/${this.commonCD}s`;
      } else {
        return '空闲中';
      }
    }
  }

  /** 当前施法状态显示进度(0-100) */
  public get castingPercent() {
    if (this.casting) {
      if (this.casting.type === 'duration') {
        return (this.castRelease / this.castMax) * 100;
      }
      return (1 - this.castRelease / this.castMax) * 100;
    } else {
      if (globalSetting.showCommonCD) {
        return (this.commonCDRelease / this.commonCD) * 100;
      } else {
        return 0;
      }
    }
  }

  battle: Battle;
  entity: Entity;

  eventUseSkill: SHEvent<Skill> = new SHEvent();
  constructor(entity: Entity) {
    this.entity = entity;
    this.battle = entity.battle;

    world.inputManager.eventKey.on((key) => {
      if (entity.panel_skill[key]) {
        this.useSkill(entity.panel_skill[key]);
      }
      if (key === 'interrupt') {
        this.interrupt();
        this.nextCast = undefined;
      }
    });

    entity.eventUpdate.on(() => {
      Object.values(entity.skills).forEach((skill) => {
        if (skill.cd > 0) {
          skill.cdRelease -= this.battle.deltaTime;
        }
      });
      if (this.casting) {
        this.castRelease -= this.battle.deltaTime;
        // 如果是引导技能, 引导期间持续回调事件
        if (this.casting.type === 'duration') {
          this.casting.eventDuration.emit();
        }

        if (this.castRelease < 0) {
          // 放出当前技能
          this.castSkill(this.casting);

          this.casting = undefined;
          // 如果有下一个待释放的技能, 释放下一个技能
          if (this.nextCast) {
            this.beginCast(this.nextCast);
            this.nextCast = undefined;
          }
        }
      }
      if (this.commonCDRelease > 0) {
        this.commonCDRelease -= this.battle.deltaTime;
        // 公cd冷却好了, 且目前没有在施法, 则释放下一个技能
        if (this.commonCDRelease < 0 && !this.casting) {
          if (this.nextCast) {
            this.beginCast(this.nextCast);
            this.nextCast = undefined;
          }
        }
      }
    });
  }

  interrupt() {
    if (this.casting) {
      this.casting.eventCancel.emit();
      this.casting = undefined;
    }
  }

  /** 尝试开始释放一个技能, 对于玩家来说,代表按下按键的一刻 */
  useSkill(skill: Skill) {
    // 检查可用性
    if (!skill.eventCanUse.emit()) {
      return;
    }

    // 即时类型可以无条件直接释放
    if (skill.type === 'effect') {
      this.beginCast(skill);
    }

    // 未处于施法中, 未在共cd中, 未被击晕, 开始释放这个技能
    if (!this.casting && this.commonCDRelease <= 0 && this.stuckRelease <= 0) {
      this.beginCast(skill);
      return;
    }

    // 如果这个条还有1s以内就读完了,那么下一个技能进入缓存队列而非立即释放
    const castQueuelimit = 1;

    if (this.casting) {
      // 如果快读完条了,那么全进施法队列缓存
      if (this.castRelease < castQueuelimit) {
        this.nextCast = skill;
      }
      // 如果当前正在释放的技能是引导技能,那么任何技能都可以打断它
      else if (this.casting.type === 'duration') {
        this.interrupt();
        this.beginCast(skill);
      }
      // 如果是瞬发技能, 可以打断当前施法并释放
      else if (skill.type === 'instance') {
        this.interrupt();
        this.beginCast(skill);
      }
    }

    // 如果当前处于公cd, 或被击晕, 则进入缓存
    if (this.commonCDRelease > 0 || this.stuckRelease > 0) {
      this.nextCast = skill;
      return;
    }
  }

  /** 开始释放一个技能 */
  beginCast(skill: Skill) {
    // 给技能一个指向的目标
    if (this.entity.target) {
      skill.target = this.entity.target;
    } else {
      shlog.warnInGame(`当前角色${this.entity.id}没有指向目标`, this.entity);
    }

    // 检查可用性
    if (!skill.eventCanUse.emit()) {
      return;
    }

    // 即时类型可以无条件直接释放,不产生任何副作用
    if (skill.type === 'effect') {
      this.castSkill(skill);
      return;
    }

    // 如果是需要读条的技能,则进入读条状态, 读条技能没有公cd
    if (skill.type === 'cast') {
      this.casting = skill;
      this.castMax = skill.castCost;
      this.castRelease = skill.castCost;
      return;
    }
    // 引导类技能开始读条和施法类本质没啥区别, 有区别也是显示
    if (skill.type === 'duration') {
      this.casting = skill;
      this.castMax = skill.castCost;
      this.castRelease = skill.castCost;
      // 引导技能开始引导的时候就通知battle
      this.eventUseSkill.emit(skill);
      return;
    }

    // 瞬发技能直接释放, 走公cd
    if (skill.type === 'instance') {
      this.castSkill(skill);
      this.commonCDRelease = this.commonCD;
    }
  }

  /** 技能真正释放的一刻 */
  castSkill(skill: Skill) {
    // 检查目标可用性
    if (!skill.target.canSelect) {
      shlog.warnInGame(`技能 [${skill.id}] 指向目标不可被选择`);
      return;
    }
    // 释放技能
    skill.eventUse.emit();
    // 通知battle
    this.eventUseSkill.emit(skill);
  }
}
