/* eslint-disable jsx-a11y/alt-text */
export function RIcon(props: {
  src: string;
  percent: number;
  center?: string;
  width?: number;
}) {
  const { src, percent, center } = props;
  let width = props.width || 64;
  let div1_h = (width * percent) / 100;
  let div2_h = width - div1_h;

  return (
    <div
      style={{
        width: width,
        height: width,
      }}
    >
      <div
        style={{
          width: width,
          height: width,
          position: 'absolute',
        }}
      >
        <img
          style={{
            width: width,
            height: width,
            position: 'absolute',
            zIndex: -2,
          }}
          src={`/assets/${src}.png`}
        />
        {center && (
          <div
            style={{
              position: 'absolute',
              left: width * 0.2,
              top: width / 2 - 8,
            }}
          >
            <div
              style={{
                fontSize: 16,
                fontWeight: 'bold',
                color: 'white',
              }}
            >
              <div
                style={{
                  width: width / 2,
                  height: 20,
                  backgroundColor: 'rgba(0, 0, 0, 0.6)',
                  position: 'absolute',
                  zIndex: -1,
                }}
              ></div>
              {center}
            </div>
          </div>
        )}
        <div
          style={{
            width: width,
            height: div1_h,
          }}
        ></div>
        <div
          style={{
            width: width,
            height: div2_h,
            backgroundColor: 'rgba(0, 0, 0, 0.6)',
          }}
        ></div>
      </div>
    </div>
  );
}
