import { World } from '.';
import { SHEvent } from '../utils';
import { Character } from './player';

export interface LocaleData {
  id: string;
  /** 当前场合允许的行为 */
  enableActions: string[];

  desc: string;
}

export class Locale {
  world: World;
  desc: string;
  id: string;
  characters: { [key: string]: Character };
  enableActions: string[];
  constructor(world: World, data: LocaleData) {
    this.world = world;
    this.desc = data.desc;
    this.characters = {};
    this.id = data.id;
    this.enableActions = data.enableActions;
  }
}
