/** 这里是游戏用到的外部接口, 接入其他平台时,需要在其他平台上实现这些接口 */
export interface SHAPI {
  setStorage(key: string, value: any): void;
  getStorage(key: string): string | null;
  getStorageKeys(): string[];
  removeStorage(key: string): void;
  downloadFile?(fileName: string, content: string): void;
  loadFileText(path: string): Promise<string>;
  eventKeyUp(callback: (key: string) => void): void;
}

export class SHAPIUnity implements SHAPI {
  eventKeyUp(callback: (key: string) => void): void {
    return;
  }
  setStorage(key: string, value: any): void {}
  getStorage(key: string): string | null {
    return '';
  }
  getStorageKeys(): string[] {
    return [];
  }
  removeStorage(key: string): void {}
  loadFileText(path: string): Promise<string> {
    return new Promise((resolve) => {
      resolve('');
    });
  }
}

/** 使用到的native能力, 其他平台换成其他实现 */
export class SHAPIWeb implements SHAPI {
  eventKeyUp(callback: any): void {
    // 发送事件只发送标准按键
    document.addEventListener('keyup', (e) => {
      callback(e.key);
    });
  }
  setStorage(key: string, value: any): void {
    let toSet = typeof value === 'string' ? value : JSON.stringify(value);
    localStorage.setItem(key, toSet);
  }
  getStorage(key: string): string | null {
    return localStorage.getItem(key);
  }
  getStorageKeys(): string[] {
    let res = [];
    for (let i = 0; i < localStorage.length; i++) {
      res.push(localStorage.key(i) as string);
    }
    return res;
  }
  removeStorage(key: string): void {
    localStorage.removeItem(key);
  }

  /** 以资源文件根目录为准, 传入相对路径, 返回文件文本, 对于web来说是 public/ */
  loadFileText(path: string): Promise<string> {
    path = `../${path}`;
    return new Promise((resolve, reject) => {
      fetch(path)
        .then((response) => {
          resolve(response.text());
        })
        .catch((err) => {
          shlog.errorInGame(`path:${path}, Request Failed`, err);
          reject(err);
        });
    });
  }

  downloadFile(fileName: string, content: string): void {
    const a = document.createElement('a');
    const event = document.createEvent('MouseEvents');
    const blob = new Blob([content]);
    a.href = URL.createObjectURL(blob);
    a.download = fileName;
    event.initEvent('click', true, true);
    a.dispatchEvent(event);
  }
}
