import { EntityData, Entity } from '../../core/battle';

globalData.buff.registe({
  id: '充盈',
  type: 'positive',
  desc: '当前经脉状态充盈, 每秒额外提升1/s筑基进度',
});

const mais_id = [
  '任脉',
  '督脉',
  '冲脉',
  '带脉',
  '阴跷脉',
  '阳跷脉',
  '阴维脉',
  '阳维脉',
];

let index = 0;
creater.entity.registe('mai', (battle) => {
  const data: EntityData = {
    id: mais_id[index],
    hp: 100,
    hpmax: 300,
    skills: [],
  };
  index++;

  const e = new Entity(battle, data);

  battle.eventPerSecond.on(() => {
    if (e.hpbar.value >= e.hpbar.maxValue * 0.8) {
      battle.progress += 1;
      e.createBuff('充盈');
    } else {
      e.removeBuff('充盈');
    }

    if (e.buffs['zjPlayerV2_暗伤']) {
      let dmg = e.buffs['zjPlayerV2_暗伤'].stack * 1;
      e.hit(dmg);
    }
  });

  e.eventDead.on(() => {
    shlog.warn(`经脉受损，筑基失败！`);
    battle.state.emit('fail');
  });

  return e;
});
