import { useState, useEffect } from 'react';
import { SHEvent, SHRef } from '../core/utils';

/** 监听一个event, 当event发生时重新渲染这个组件 */
export function useUpdateOn<T>(event: SHEvent<T> | SHRef<T>) {
  const [data, setData] = useState(0);
  useEffect(() => {
    function handleUpdate(p: T) {
      setData(data + 1);
    }
    event.on(handleUpdate);
    return () => {
      event.off(handleUpdate);
    };
  });
}

export function useEmitterOn<T>(
  event: SHEvent<T> | SHRef<T>,
  callback: (p: T) => void
) {
  useEffect(() => {
    function handleUpdate(p: T) {
      callback && callback(p);
    }
    event.on(handleUpdate);
    return () => {
      event.off(handleUpdate);
    };
  });
}

/**
 * 当监听的SHRef的值改变后, 触发setData和更新
 * const [data,setData] = useSHRef(battle.iii)
 */
export function useSHRef<T>(ref: SHRef<T>) {
  const [data, setData] = useState(ref.value);

  function rSetData(v: T) {
    ref.value = v;
  }

  useEffect(() => {
    function handleUpdate(value: T) {
      setData(value);
    }
    ref.on(handleUpdate);
    return () => {
      ref.off(handleUpdate);
    };
  });

  return [data, rSetData];
}
