export class SHRef<T> {
  _value: T;
  _event: SHEvent<T>;

  public set value(v: T) {
    if (this._value === v) {
      return;
    }
    this._value = v;
    this._event.emit(v);
  }
  public get value() {
    return this._value;
  }

  constructor(value: T, id?: string) {
    this._value = value;
    this._event = new SHEvent(id);
  }

  /** 使用on可以监听到这个值的改变, 使用set设置这个值而非emit */
  on(callback: (v: T) => void) {
    this._event.on(callback);
  }

  off(callback: (v: T) => void) {
    this._event.off(callback);
  }

  once(callback: (v: T) => void) {
    this._event.once(callback);
  }

  emit(v: T) {
    if (this._value === v) {
      return;
    }
    this._value = v;
    this._event.emit(v);
  }
}

export class SHEvent<P = void> {
  callbacks: Set<(p: P) => void>;
  callbackCount: number = 0;
  id?: string;

  /** 后续考虑把event都加上一个id,便于追踪到底是哪个event */
  constructor(id?: string) {
    this.callbacks = new Set();
    this.id = id;
  }

  on(callback: (p: P) => void) {
    this.callbacks.add(callback);
    this.callbackCount++;
  }

  off(callback: (p: P) => void) {
    this.callbacks.delete(callback);
    this.callbackCount--;
  }

  emit(p: P) {
    if (this.callbackCount > 0) {
      this.callbacks.forEach((callback) => {
        callback(p);
      });
    }
  }

  once(callback: (p: P) => void) {
    const back = (p: P) => {
      callback(p);
      this.off(back);
    };
    this.on(back);
  }
}

export class EventCanUse<P = void> {
  callbacks: Set<(p: P) => boolean>;
  constructor() {
    this.callbacks = new Set();
  }

  on(callback: (p: P) => boolean) {
    this.callbacks.add(callback);
  }

  off(callback: (p: P) => boolean) {
    this.callbacks.delete(callback);
  }

  emit(p: P): boolean {
    let canUse = true;
    this.callbacks.forEach((callback) => {
      const rr = callback(p);
      if (!rr) {
        canUse = false;
      }
    });
    return canUse;
  }
}
