import { Button, Select, Tabs, Tag, Tooltip } from 'antd';
import { Character } from '../../core/world/player';
import { RProgress } from '../common/RProgress';
import { RRefLabel } from '../common/RRefLabel';
import { useEmitterOn, useUpdateOn } from '../hook';
import { RSave } from '../systen/RSave';
import { RBag } from './RBag';
import { RBattleField } from './RBattleField';
import { RBattlePrepare } from './RBattlePrepare';
import { RCultivate } from './RCultivate';
import { RDataDebug } from './RDataDebug';
import { RShop } from './RShop';
const { TabPane } = Tabs;
const { Option } = Select;

function RLevelCard(props: { character: Character }) {
  const player = props.character;
  const percent = (player.exp / player.levelData.expmax) * 100;
  return (
    <div
      style={{ display: 'flex', flexDirection: 'row', alignItems: 'center' }}
    >
      <div>{player.levelData.name}</div>
      <RProgress
        style={{ width: 300 }}
        percent={percent}
        height={30}
        title={`${player.exp}/${player.levelData.expmax}(${percent.toFixed(
          0
        )}%)`}
      ></RProgress>
    </div>
  );
}

export function RWorld() {
  useUpdateOn(world.activeAction);
  useUpdateOn(world.eventUIUpdate);

  const tabs_jsx: any = {
    修炼: RCultivate(),
    灵药堂: RShop(),
    战斗: RBattleField(),
  };

  const currentLocale = world.player.locale.id;

  return (
    <div>
      <RLevelCard character={world.player} />
      <RRefLabel obj={world.player} propkey={['talent', 'id']}></RRefLabel>
      <div style={{ margin: 16 }}>
        点击前往地点:
        {Object.keys(world.locales).map((key) => {
          return (
            <Tooltip key={key} title={world.locales[key].desc}>
              <Tag
                color={key === world.player.locale.id ? 'blue' : 'default'}
                onClick={() => {
                  world.player.moveToLocal(key);
                }}
              >
                {key}
              </Tag>
            </Tooltip>
          );
        })}
      </div>

      <Tabs
        tabPosition="left"
        size="small"
        activeKey={world.activeAction.value}
        onChange={(key: any) => {
          world.activeAction.emit(key);
        }}
      >
        <TabPane tab={'个人信息'} key="个人信息">
          <RBag />
        </TabPane>
        {world.player.locale.enableActions.map((key) => {
          return (
            <TabPane tab={key} key={key}>
              {tabs_jsx[key]}
            </TabPane>
          );
        })}
        <TabPane tab={'保存游戏'} key="保存游戏">
          <RSave enableSave={true} />
        </TabPane>
        <TabPane tab={'战斗准备'} key="战斗准备" disabled>
          <RBattlePrepare />
        </TabPane>
        {globalSetting.enableDebug && (
          <TabPane tab={'debug'} key="debug">
            <RDataDebug></RDataDebug>
          </TabPane>
        )}
      </Tabs>
    </div>
  );
}
