export class ZJPlayer extends Entity {
  battle: Battle;

  淬脉使用次数: number = 0;
  筑基丹使用次数: number = 0;

  constructor(battle: Battle, data: EntityData) {
    super(battle, data);
    this.battle = battle;

    // 将skill同时加入所有skill和展示键盘skill
    const addSkill = (id: string, key: string) => {
      const s = this.createSkill(id);
      // 加入面板技能组
      this.panel_skill[key] = s;
      return s;
    };

    addSkill('行气', 'skill2_1');
    addSkill('运气', 'skill2_2');

    let skill = addSkill('周天运转', 'skill2_3');
    let heal = 20;
    skill.eventUse.on(() => {
      Object.values(this.getTeams()).forEach((jingmai) => {
        if (world.config.difficult === 'test') {
          jingmai.heal(heal * 20);
        } else {
          jingmai.heal(heal);
        }
      });
    });

    skill = addSkill('淬脉', 'skill1_1');
    let hit = 50;
    let addmax = 50;
    skill.eventUse.on(() => {
      if (skill.target) {
        skill.target.hit(hit);
        skill.target.hpbar.basic.maxValue += addmax;
        skill.target.hpbar.standard += addmax;
        skill.entity.battle.custom.progress -= 20;

        skill.target.createBuff('zjPlayerV2_暗伤');
        this.淬脉使用次数++;
      }
    });

    skill = addSkill('筑基丹', 'skill1_2');
    skill.useLimit = world.player.bag.find('筑基丹')?.stack || 0;
    skill.eventUse.on(() => {
      this.筑基丹使用次数++;
    });

    this.playerController = new EntityController(this);
  }
}
