import { InputManager } from './inputManager';
import { SHEvent, SHRef } from '../utils';
import { Locale } from './locate';
import { Character, Player } from './player';
import { CharacterData } from './player';
import { WorldSave } from './saves';
import { SHTime } from './time';
import { Battle } from '../battle';
import { Statistics } from './statistics';
import { GlobalSetting } from '../bootstrap';

export interface WorldConfig {
  difficult: 'test' | 'eazy' | 'normal';
}

/** 这是存档文件的数据类型 */
export interface WorldData {
  id: string;
  /** 游戏时间 */
  time: number;
  /** 现实时间 */
  realTime: number;
  /** 现实时间的格式化 */
  realTimeFormat: string;

  player: CharacterData;
  /** 游戏设置 */
  config: WorldConfig;

  /** 存档时的游戏版本 */
  version: string;
  /** 统计信息 */
  statistics: { [key: string]: number };
}

/** 当前激活的一级tab */
export type WorldActiveTab = '世界' | '战斗';
/** 当前激活的二级tab */
export type WorldAction = '个人信息' | '修炼' | '保存游戏' | '战斗准备';
/** 当前世界的状态, 正在初始化 | 暂停中 | 运行中 */
export type WorldState = 'init' | 'pause' | 'running';

export class World {
  /** 当前战斗 */
  battle!: Battle;
  /** 下一场战斗 */
  nextBattle?: string;
  /** 世界时间 */
  time: SHTime;
  /** world时间更新了一天 */
  eventUpdate: SHEvent<number> = new SHEvent();
  eventUpdateMoon: SHEvent<number> = new SHEvent();
  eventUpdateYear: SHEvent<number> = new SHEvent();
  /** 重绘整个UI,讲道理只要不是每帧都重绘, 开销可以忽略不计 */
  eventUIUpdate: SHEvent = new SHEvent();

  /** 设置 */
  config!: WorldConfig;
  /** 键盘输入管理器 */
  inputManager: InputManager;
  /** 存读档系统 */
  saves: WorldSave;
  /** 地点 */
  locales: { [key: string]: Locale } = {};
  /** 当前激活的一级tab */
  activeTab: SHRef<WorldActiveTab> = new SHRef<WorldActiveTab>('世界');
  /** 当前激活的二级tab */
  activeAction: SHRef<WorldAction> = new SHRef<WorldAction>('个人信息');
  /** 当前世界的状态, 正在初始化 | 暂停中 | 运行中 */
  state: SHRef<WorldState> = new SHRef<WorldState>('init');

  simUsedTime: number = 0;
  /** 统计信息 */
  statistics: Statistics;
  /** 玩家 */
  // @ts-ignore
  player: Player;
  /** npc */
  characters: { [key: string]: Character } = {};

  constructor() {
    this.inputManager = new InputManager();
    this.saves = new WorldSave(this);
    this.time = new SHTime(this);
    this.statistics = new Statistics(this);
    Object.keys(globalData.locale._data).forEach((id) => {
      this.locales[id] = new Locale(this, globalData.locale.get(id));
    });
  }

  /** 时间演化推进, 执行过程会卡死主现场, 后续考虑每个月释放一次 UIUpdate */
  addTime(day: number) {
    this.state.value = 'running';
    let start = new Date().getTime();
    for (let i = 0; i < day; i++) {
      if (this.state.value === 'running') {
        // 一天一天快进
        this.time.add(1);
        this.eventUpdate.emit(this.time.time);
      }
    }
    this.simUsedTime = new Date().getTime() - start;
    shlog.info(
      `时间快进${day}天, 现在为:${this.time.timeStr}, 耗时${this.simUsedTime}ms`
    );
    this.state.value = 'pause';
    this.eventUIUpdate.emit();
  }

  /** 只有从world可以创建battle */
  startBattle(id: string) {
    let battle = creater.battle.create(id, this);
    this.battle = battle;
    this.battle.init();
  }
}
