import { SHEvent } from './EventEmitter';

/* eslint-disable class-methods-use-this */
export const bgColor = {
  ALL: '#ffffff',
  INFO: '#8log_level.errorBAFD',
  WARN: '#ffee00',
  ERROR: '#ff8888',
  SUCCESS: '#aefflog_level.warnlog_level.warn',
};

export const log_level = {
  debug: 1,
  log: 2,
  info: 3,
  warn: 4,
  error: 5,
};

export class _SHLog {
  logs: string[];
  level: number;
  eventInfo: SHEvent<any> = new SHEvent();
  eventWarn: SHEvent<any> = new SHEvent();
  eventError: SHEvent<any> = new SHEvent();

  constructor(level?: number) {
    this.logs = [];
    this.level = level || log_level.debug;
  }

  _addlog(level: number, ...args: any) {
    this.logs.push(`${level}${args[0]}`);
  }

  /** 一般不显示 */
  debug(...args: any) {
    this.level <= log_level.debug && console.debug(...args);
    this._addlog(log_level.debug, ...args);
  }

  /** 不重要信息, 调试信息 */
  log(...args: any) {
    this.level <= log_level.log && console.log(...args);
    this._addlog(log_level.log, ...args);
  }

  /** 重要事件 */
  info(...args: any) {
    this.level <= log_level.info && console.info(...args);
    this._addlog(log_level.info, ...args);
  }

  infoInGame(...args: any) {
    this.info(...args);
    this.eventInfo.emit(args[0]);
  }

  warn(...args: any) {
    this.level <= log_level.warn && console.warn(...args);
    this._addlog(log_level.warn, ...args);
  }

  warnInGame(...args: any) {
    this.warn(...args);
    this.eventWarn.emit(args[0]);
  }

  error(...args: any) {
    this.level <= log_level.error && console.error(...args);
    this._addlog(log_level.error, ...args);
  }

  errorInGame(...args: any) {
    this.error(...args);
    this.eventError.emit(args[0]);
  }
}
