/*
  筑基V2
V1的问题在于反直觉, 且难度和压迫感不合理. 

筑基进度 0/100, 基础,每秒+2进度
充盈: 当血量高于50%时, 每经脉,每秒+1进度

敌方技能:
流失: 每秒对随机目标造成10伤害, 每秒伤害提升1%
紊乱: 对三个目标施加, 4s后造成 60伤害, 冷却12
进度50%之后开始释放: 衰朽, 对生命最低的目标造成伤害

己方技能:

行气
运气
周天运转
逆行经脉: 引导技能, 持续8s, 群体大量回血
淬脉 读条2s: 对一个经脉释放, 对齐造成50伤害, 并提升50hp上限. 附加暗伤效果, 每秒流失1点生命
筑基丹: 瞬发: 单体极大治疗
 */

import { Battle } from '../../core/battle';
import './zjplayer';
import './mai';
import './enemy';

creater.battle.registe('筑基', (world) => {
  const battle = new Battle(world, {
    id: '筑基',
    help: `游戏说明: 
  1.上方是筑基进度条, 筑基进度满了之后筑基成功.
  2.任意经脉血量为空时筑基失败.
  3.经脉血量高于80%时,触发充盈效果,额外加快筑基速度.
  4.筑基丹是极高效的单体治疗技能, 在游戏过程中尽可能搜集筑基丹,并在筑基中使用.
  5.淬脉会产生很大的副作用,但会让经脉强韧, 在筑基成功后,每淬脉了一条经脉都会获得额外的永久血量加成.
  
  战斗将在使用第一个技能之后自动开始
  tips: 将鼠标放在对应区域悬停可以看到详细说明, 允许随时暂停.
  `,
    userInterface: '筑基',
    custom: {
      progress: 0,
      progressMax: 500,
    },
  });
  const addSpeed = world.config.difficult === 'test' ? 50 : 5;

  for (let i = 0; i < 8; i++) {
    battle.createEntity('mai', 'left');
  }

  // 敌人
  battle.createEntity('zjEnemy', 'right');
  // 玩家
  battle._player = battle.createEntity('zjPlayerV2', 'left');
  // 玩家不在场上, 创建后删掉
  battle.lefts.pop();

  battle.eventPerSecond.on(() => {
    battle.custom.progress += addSpeed;
  });

  battle.eventUpdate.on(() => {
    if (battle.custom.progress >= battle.custom.progressMax) {
      shlog.info(`筑基进度满, 筑基成功!`);
      battle.state.emit('success');
    }
  });

  return battle;
});
