import { SHEvent } from '../utils';
import { Character } from './player';
import { statisticKeys } from './statistics';

export type ItemType = '草药' | '丹药' | '功法';

export interface ItemData {
  type: ItemType;
  id: string;
  rarity: number;
}

export class Item {
  /** 物品id,可以用名字做id,特殊物品可以用名字_key做id */
  id: string;
  /** 物品类型, 丹药 法器 材料等, 丹药也要细分战斗类还是修炼类了. */
  type: ItemType;

  /** 稀有度 */
  rarity: number;

  /** 堆叠, 考虑允许物品无限堆叠 */
  stack: number = 0;

  bag: Bag;
  constructor(bag: Bag, data: ItemData) {
    this.bag = bag;
    this.id = data.id;
    this.type = data.type;
    this.rarity = data.rarity;
  }
}

export interface BagData {}

export class Bag {
  /** 拥有灵石 */
  money: number = 0;
  items: Item[] = [];
  /** 增加一个key索引, 允许通过key来快速找到一个item,然后堆叠 */
  items_map: { [key: string]: Item } = {};

  character: Character;

  eventAddItem: SHEvent = new SHEvent();

  constructor(c: Character, data: BagData) {
    this.character = c;
    this.eventAddItem.on(() => {
      world.statistics.add(statisticKeys.获得物品);
    });
  }
  save(): BagData {
    return {};
  }

  /** 增加物品会同时增加物品和map的引用 */
  addItem(id: string, count?: number) {
    const c = count || 1;
    if (this.items_map[id]) {
      this.items_map[id].stack += c;
    } else {
      const item = new Item(this, globalData.item.get(id));
      this.items.push(item);
      this.items_map[id] = item;
      item.stack = c;
    }
    this.eventAddItem.emit();
  }

  find(key: string) {
    return this.items_map[key];
  }

  /** 移除物品时只移除stack,不移除map和array,考虑存档时再进行清理 */
  removeItem(id: string, count?: number) {
    const c = count || 1;
    if (this.items_map[id]) {
      if (this.items_map[id].stack > 0) {
        this.items_map[id].stack -= c;
      } else {
        shlog.errorInGame(`已经为0了,产生了意外的移除`);
      }
    } else {
      shlog.errorInGame(`未找到待移除物品`);
    }
  }
  /** 暂时先不做不可堆叠物品, 可以用存档和data恢复的方式做 */
  addItemWithObj(item: Item) {}

  select(type: string) {
    return this.items.filter((item) => {
      return item.type === type;
    });
  }
}
