/* eslint-disable no-eval */
export class ModLoader {
  async load(id: string) {
    const uri = `mods/${id}/index.js`;

    /** 动态加载本地mod文件, 暂时先不用, 所有mod都走静态加载 */
    await shapi
      .loadFileText(uri)
      .then((text) => {
        shlog.log(
          `加载mod:${id}`
          // text.length > 100 ? text.slice(0, 100) + '...' : text
        );
        try {
          eval(text);
        } catch (e) {
          shlog.errorInGame(`mod:${id} ,js文件执行错误`, e);
        }
      })
      .catch((err) =>
        shlog.errorInGame(`mod:${id} 请求错误, Request Failed`, err)
      );
  }
}
