import { Tooltip, Progress, Tag } from 'antd';
import { Entity, Skill } from '../../core/battle';
import { RIcon } from '../common/RIcon';

export function RSkillPanel(props: { skills: { [key: string]: Skill } }) {
  return (
    <div>
      <div
        style={{
          display: 'flex',
          flexDirection: 'row',
        }}
      >
        {world.inputManager.skillFirstLine.map((shkey) => {
          return (
            <RSkillIcon key={shkey} skill={props.skills[shkey]} shkey={shkey} />
          );
        })}
      </div>
      <div
        style={{
          display: 'flex',
          flexDirection: 'row',
        }}
      >
        {world.inputManager.skillSecondLine.map((shkey) => {
          return (
            <RSkillIcon key={shkey} skill={props.skills[shkey]} shkey={shkey} />
          );
        })}
      </div>
    </div>
  );
}

export function RSkillIcon(props: { skill?: Skill; shkey: string }) {
  let skill = props.skill;
  let desc2 = world.inputManager.key_keyboard[props.shkey];

  /** 如果技能不存在, 则返回一个空方框 */
  if (!skill) {
    return (
      <div
        style={{
          width: '60px',
          display: 'flex',
          alignItems: 'center',
          flexDirection: 'column',
          marginRight: 10,
        }}
      >
        <div
          style={{
            height: 60,
            width: 60,
            border: '1px  solid #ccc',
          }}
        ></div>
        <div style={{ alignSelf: 'center' }}>空</div>
        <Tag color={'default'} style={{ alignSelf: 'center' }}>
          {desc2}
        </Tag>
      </div>
    );
  }

  const { desc, percent, center } = skill.iconData;

  return (
    <Tooltip key={skill.id} title={skill.desc}>
      <div
        style={{
          width: '60px',
          display: 'flex',
          alignItems: 'center',
          flexDirection: 'column',
          marginRight: 10,
        }}
        onClick={() => {
          skill && skill.entity.playerController?.useSkill(skill);
        }}
      >
        <RIcon
          src={skill.icon}
          percent={percent}
          center={center}
          width={60}
        ></RIcon>

        <div style={{ alignSelf: 'center' }}>{desc}</div>
        <Tag color={'geekblue'} style={{ alignSelf: 'center' }}>
          {desc2}
        </Tag>
      </div>
    </Tooltip>
  );
}
