import { Tag, Tooltip } from 'antd';
import { Entity } from '../../core/battle';
import { RProgress } from '../common/RProgress';
import { RSuperBar } from '../common/RSuperBar';

const bufftype_color = {
  normal: 'cyan',
  positive: 'success',
  negative: 'error',
};

/** 角色状态卡片, 用于展示一个Entity */
export function REntityCard(props: { entity: Entity }) {
  const { entity } = props;
  let bgcolor;
  if (entity.isSelect) {
    bgcolor = '#d1e7f0';
  } else if (!entity.canSelect) {
    bgcolor = '#bae2ba';
  } else if (entity.inDanger) {
    bgcolor = 'rgb(238, 202, 202)';
  } else {
    bgcolor = '#ffffff';
  }

  let border = 'solid 0.5px #ccc';
  if (entity.buffs['淬脉']) {
    border = 'solid 3px #ffee00';
  }

  const controller: any = entity.playerController;

  return (
    <div
      style={{
        width: 400,
        border,
        padding: 2,
        backgroundColor: bgcolor,
        margin: 8,
      }}
      onClick={() => {
        shlog.log(`当前选中目标:${entity.id}`, entity);
        entity.battle.eventSelectEntity.emit(entity.id);
      }}
    >
      <div style={{ margin: 4 }}>
        <Tag color={'blue'}>{entity.name}</Tag>
        {entity.isAlive || <Tag color={'error'}>死亡</Tag>}
        {entity.canSelect || <Tag color={'error'}>不可选中</Tag>}
        {Object.values(entity.buffs).map((buff) => {
          return (
            <Tooltip key={buff.id} title={buff.desc}>
              <Tag color={bufftype_color[buff.type]}>{buff.iconData}</Tag>{' '}
            </Tooltip>
          );
        })}
      </div>
      <RSuperBar bar={entity.hpbar} height={20}></RSuperBar>
      <RProgress
        percent={controller.castingPercent}
        title={controller.castingLabel}
        height={20}
        color="#a8a245"
      ></RProgress>
    </div>
  );
}
