import { Button, notification, Switch, Tabs } from 'antd';
import { saveSetting } from '../core/world/saves';
import { statisticKeys } from '../core/world/statistics';
import { useEmitterOn, useUpdateOn } from './hook';
import { RGame } from './RGame';
import { RSave } from './systen/RSave';
const { TabPane } = Tabs;
export function App() {
  useUpdateOn(world.state);
  // 监听全局log并弹窗
  useEmitterOn(shlog.eventWarn, (msg) => {
    setTimeout(() => {
      notification.warn({
        message: msg,
        duration: 2,
      });
    }, 0);
  });

  useEmitterOn(shlog.eventInfo, (msg) => {
    setTimeout(() => {
      notification.info({
        message: msg,
        duration: 2,
      });
    }, 0);
  });

  useEmitterOn(shlog.eventError, (msg) => {
    setTimeout(() => {
      notification.error({
        message: msg,
        duration: 2,
      });
    }, 0);
  });

  function openTestBattle(id: string) {
    world.saves.load(globalData.save.get('default'));
    world.state.emit('pause');
    world.startBattle(id);
    world.activeTab.emit('战斗');
    world.statistics.add(statisticKeys.已完成新手引导);
  }

  // 开始游戏, 渲染游戏主界面
  if (world.state.value !== 'init') {
    return <RGame />;
  } else {
    return (
      <div>
        <div style={{ margin: 100 }}>
          <div
            style={{
              fontSize: 32,
            }}
          >
            仙路
          </div>
          <div>等想好剧情了再起名字</div>
        </div>

        <Tabs tabPosition="left" size="small" defaultActiveKey="开始新游戏">
          <TabPane tab={'开始新游戏'} key="开始新游戏">
            <Button
              onClick={() => {
                world.saves.load(globalData.save.get('default'));
                world.state.emit('pause');
              }}
            >
              开始新游戏
            </Button>
            <Button
              onClick={() => {
                openTestBattle('自定义');
              }}
            >
              开始自定义战斗
            </Button>
            <Button
              onClick={() => {
                openTestBattle('demo');
              }}
            >
              demo
            </Button>
          </TabPane>

          <TabPane tab={'读取存档'} key="读取存档">
            <RSave enableSave={false} />
          </TabPane>
          <TabPane tab={'游戏设置'} key="游戏设置">
            <div>打开debug开关</div>
            <Switch
              defaultChecked={globalSetting.enableDebug}
              onChange={(value) => {
                globalSetting.enableDebug = value;
                saveSetting();
              }}
            ></Switch>
          </TabPane>
        </Tabs>
      </div>
    );
  }
}
