import { World } from '.';
import { battleKeys } from '../battle';
import { SHEvent } from '../utils';
import { Bag, BagData } from './bag';
import { Locale } from './locate';
import { statisticKeys } from './statistics';

export interface CharacterData {
  id: string;
  locale: string;
  talent: number;
  exp: number;
  level: number;
  bag: BagData;
}

export interface LevelData {
  id: string;
  name: string;
  cultivateSpeed: number;
  expmax: number;
  life: number;
  attack: number;
  breakthrough?: string;
}

export class Character {
  world: World;

  id: string;
  locale: Locale;

  talent: number;
  exp: number;
  level: number;

  public get levelName() {
    return this.levelData.name;
  }
  public get nextLevelName() {
    return globalData.level.get(this.level + 1).name;
  }

  bag: Bag;

  eventCultivate: SHEvent = new SHEvent();
  eventBreakthrough: SHEvent = new SHEvent();

  constructor(world: World, data: CharacterData, recoveryMode?: boolean) {
    this.world = world;

    shlog.info('读取player数据', data);
    // 这个写法可以保证绝对不会出错, 是收益很大的写法...
    this.id = data.id;
    this.locale = world.locales[data.locale];
    this.talent = data.talent;
    this.exp = data.exp;
    this.level = data.level;
    this.bag = new Bag(this, data.bag);
  }

  save(): CharacterData {
    return {
      id: this.id,
      locale: this.locale.id,
      talent: this.talent,
      exp: this.exp,
      level: this.level,
      bag: this.bag.save(),
    };
  }

  public get levelData() {
    return globalData.level.get(this.level);
  }

  public get cultivateSpeedByTalent() {
    return (this.talent / 100) * this.levelData.cultivateSpeed;
  }
  public get cultivateSpeed() {
    return this.levelData.cultivateSpeed + this.cultivateSpeedByTalent;
  }

  cultivate(time: number) {
    this.exp += this.cultivateSpeed * time;
    world.addTime(time);
    this.eventCultivate.emit();
  }

  public get canBreakthrough() {
    return this.exp >= this.levelData.expmax;
  }

  /** 目标level */
  breakthrough() {
    if (!this.canBreakthrough) {
      shlog.warnInGame('经验不够, 不满足突破条件');
    }
    if (!this.levelData.breakthrough) {
      this.breakthroughSuccess();
    } else {
      if (this.level === 3) {
        world.nextBattle = battleKeys.筑基;
      } else if (this.level === 6) {
        world.nextBattle = battleKeys.金丹;
      } else if (this.level === 9) {
        world.nextBattle = battleKeys.元婴;
      } else {
        shlog.warnInGame('尚未开发, 请期待后续');
        return;
      }
      shlog.infoInGame(`准备突破下一层:${this.nextLevelName}`);
      world.activeAction.emit('战斗准备');
    }
  }

  breakthroughSuccess() {
    this.exp -= this.levelData.expmax;
    this.level++;
    shlog.infoInGame(`成功突破至:${this.levelName}`);
    world.eventUIUpdate.emit();
    this.eventBreakthrough.emit();
  }

  moveToLocal(toId: string) {
    if (toId === this.locale.id) {
      shlog.warnInGame('不要原地tp');
      return;
    }
    // 加入新的区域
    this.world.locales[toId].characters[this.id] = this;
    // 设置角色的区域
    this.locale = this.world.locales[toId];
    // 删除原来的区域
    delete this.locale.characters[this.id];

    this.world.eventUIUpdate.emit();
  }
}

export class Player extends Character {
  constructor(world: World, data: CharacterData) {
    super(world, data);
    this.eventCultivate.on(() => {
      world.statistics.add(statisticKeys.闭关修炼次数);
    });
    this.eventBreakthrough.on(() => {
      world.statistics.add(statisticKeys.突破次数);
    });
  }
}
