import { Battle, Buff, BuffData, Skill, SkillData } from '.';
import { SuperBar } from './common/superBar';
import { SHEvent } from '../utils';
import { EntityController } from './common/controller';
import { PluginRegister, CommonData, CommonObject } from '../system';

export type EntitySide = 'left' | 'right' | 'others';

export interface EntityData extends CommonData {
  hp?: number;
  hpmax?: number;
  skills?: string[];
  needController?: boolean;
}

export class Entity extends CommonObject<EntityData> {
  side: EntitySide;

  hpbar: SuperBar;

  battle: Battle;
  isSelect: boolean = false;
  target?: Entity;

  /** 角色拥有的所有技能 */
  skills: { [key: string]: Skill } = {};

  eventUpdate: SHEvent = new SHEvent();
  eventDead: SHEvent = new SHEvent();

  /** 使用键盘 */
  panel_skill: { [key: string]: Skill } = {};

  isAlive: boolean = true;
  canSelect: boolean = true;

  buffs: { [key: string]: Buff } = {};

  playerController?: EntityController;

  /** 处于危险中 */
  inDanger: boolean = false;

  constructor(battle: Battle, data: EntityData) {
    super(data);
    this.battle = battle;
    this.side = 'others';

    this.battle.eventUpdate.on(() => {
      this.eventUpdate.emit();
    });

    this.hpbar = new SuperBar({
      id: 'hp',
      value: data.hp || 100,
      maxValue: data.hpmax || 100,
    });
    this.hpbar.eventBasicEmpty.on(() => {
      this.isAlive = false;
      this.eventDead.emit();
    });

    data.skills &&
      data.skills.forEach((key) => {
        this.createSkill(key);
      });

    if (data.needController) {
      this.playerController = new EntityController(this);
    }
  }

  getEnemys() {
    if (this.side === 'left') {
      return this.battle.rights.filter((e) => e.canSelect);
    } else {
      return this.battle.lefts.filter((e) => e.canSelect);
    }
  }

  getTeams() {
    if (this.side === 'left') {
      return this.battle.lefts.filter((e) => e.canSelect);
    } else {
      return this.battle.rights.filter((e) => e.canSelect);
    }
  }

  /** 治疗这个单位，相当于给bar的basic部分增加值 */
  heal(value: number) {
    // 挂了或者不可选中,无法被治疗
    if (!this.isAlive || !this.canSelect) {
      return;
    }
    this.hpbar.basic.add(value);
  }

  hit(value: number) {
    // 挂了或者不可选中,无法被治疗或伤害
    if (!this.isAlive || !this.canSelect) {
      return;
    }
    this.hpbar.minus(value);
  }

  addSkill(skill: Skill): Skill {
    this.skills[skill.id] = skill;
    return skill;
  }

  createSkill(id: string): Skill {
    const skill = creater.skill.create(id, this);
    return this.addSkill(skill);
  }

  /** buff由于涉及一个同类buff应该叠加而非重新new的问题, 因此不提供addBuff */
  createBuff(id: string): Buff {
    let buff = this.buffs[id];
    if (!buff) {
      buff = creater.buff.create(id, this);
      this.buffs[id] = buff;
    } else {
      buff.release = buff.releaseMax || -1;
      if (buff.stack < buff.stackMax) {
        buff.stack++;
      }
    }
    return buff;
  }

  removeBuff(id: string) {
    delete this.buffs[id];
  }
}
