import { Modal, Tag } from 'antd';
import exp from 'constants';
import { RDebug } from '../common/RDebug';
import { RActions } from '../common/RRefLabel';

/** 弹窗提示文字 */
export function info(title: string, msg: string, callback?: any) {
  const divs = msg.split('\n').map((str) => {
    return <p key={`${Math.random()}`}>{str}</p>;
  });
  Modal.info({
    title,
    content: divs,
    onOk: () => {
      callback && callback();
    },
  });
}

/** 如果没有命中过打点key, 则弹出battle的帮助, 并添加打点 */
export function useFirstInfo(key: string) {
  const battle = world.battle;
  if (!world.statistics.get(key)) {
    info('帮助', battle.help);
    world.statistics.add(key);
  }
}

/** 最上面的debug信息,有一些当前battle的状态 */
export function RBattleDebug() {
  const battle = world.battle;
  return (
    <RDebug>
      <div style={{ height: 30 }}>
        <Tag>difficult:{world.config.difficult}</Tag>
        <Tag>BattleState:{battle.state.value}</Tag>
        <Tag>battleStartTime:{battle.battleStartTime.toFixed(1)}</Tag>
        <Tag>realtime:{battle.realTimeInSecend.toFixed(1)}</Tag>
        <Tag>updateCount:{battle.eventUpdate.callbackCount}</Tag>
      </div>
      <div style={{ height: 30 }}>
        <Tag>target:{battle.player.target?.id}</Tag>
        <Tag>casting:{battle.player.playerController?.casting?.id}</Tag>
        <Tag>castNext:{battle.player.playerController?.nextCast?.id}</Tag>
        update耗时: <Tag>{battle.simUsedTime.toFixed(2)}ms</Tag>
      </div>
    </RDebug>
  );
}

/** battle进入控制条 */
export function RBattleControl() {
  const battle = world.battle;
  return (
    <RActions
      actions={{
        开始: () => {
          battle.state.emit('running');
        },
        暂停: () => {
          battle.state.emit('paused');
        },
        帮助: () => {
          info('帮助', battle.help);
        },
      }}
    ></RActions>
  );
}
