/* eslint-disable jsx-a11y/alt-text */
import { useEmitterOn, useUpdateOn } from '../hook';
import { REntityCard, RPlayerDetails, RSkillPanel } from '.';
import { Modal, Space, Tag, Image } from 'antd';
import { Battle, Entity, EntityController } from '../../core/battle';
import { RDBM } from './RDBM';
import { statisticKeys } from '../../core/world/statistics';
import {
  info,
  RBattleControl,
  RBattleDebug,
  useFirstInfo,
} from './RBattleCommon';
import './common.css';
import { RIcon } from '../common/RIcon';

// 通用战斗
export function RBattle() {
  const battle: Battle = world.battle;
  const player: Entity = battle.player;
  const enemy = battle.mainEnemy;

  useUpdateOn(battle?.eventUpdate);
  useUpdateOn(battle?.eventSelectEntity);

  useEmitterOn(battle?.state, (res) => {
    if (res === 'success' || res === 'fail') {
      let msg = `当前战斗结果:${res}
战斗时长:${battle.battleStartTime.toFixed(2)}
          `;
      info('战斗结束', msg, () => {
        world.activeTab.emit('世界');
        if (battle.state.value === 'success') {
          world.player.breakthroughSuccess();
        }
      });
    }
  });

  if (!battle) {
    return <div>未初始化battle</div>;
  }

  return (
    <div>
      <RBattleDebug />
      <RBattleControl />
      <Space></Space>
      {/* <RDBM
        data={Object.values(enemy.skills).map((skill) => {
          return {
            title: skill.name,
            release: skill.cdRelease,
            total: skill.cd,
          };
        })}
      ></RDBM> */}

      <div style={{ display: 'flex', flexDirection: 'row' }}>
        <div style={{ display: 'flex', flexDirection: 'column' }}>
          {Object.values(battle.lefts).map((entity) => {
            return <REntityCard key={entity.id} entity={entity} />;
          })}
        </div>

        <div style={{ display: 'flex', flexDirection: 'column' }}>
          {Object.values(battle.rights).map((entity) => {
            return <REntityCard key={entity.id} entity={entity} />;
          })}
        </div>
      </div>

      <RPlayerDetails
        controller={battle.player.playerController as EntityController}
      ></RPlayerDetails>
    </div>
  );
}
