import React from 'react';

export function RProgress(props: {
  percent: number;
  title?: string;
  height?: number;
  width?: number;
  color?: string;
  style?: React.CSSProperties;
}) {
  let cssRight = 100 - props.percent;
  if (cssRight < 0) {
    cssRight = 0;
  }
  if (cssRight > 100) {
    cssRight = 100;
  }
  const height = props.height || 45;
  const fontSize = Math.round((16 * height) / 45);
  let container: React.CSSProperties = {
    height: `${height}px`,
    background: '#dedede',
    position: 'relative',
    overflow: 'hidden',
    borderRadius: 4,
    margin: 4,
    width: props.width,
    ...props.style,
  };
  let fg: React.CSSProperties = {
    width: '100%',
    height: `${height}px`,
    background: props.color || '#1AAAA8',
    position: 'absolute',
    right: `${cssRight}%`,
  };
  let title: React.CSSProperties = {
    position: 'absolute',
    width: 'auto',
    height: `${height}px`,
    left: '15px',
    color: '#ffffff',
    lineHeight: `${height}px`,
    fontSize: `${fontSize}px`,
  };
  return (
    <div style={container}>
      <div style={fg}></div>
      <div style={title}>{props.title}</div>
    </div>
  );
}
