import { Card, Button, Popconfirm } from 'antd';
import moment from 'moment';
import React, { useCallback, useMemo, useState, memo } from 'react';
import { WorldData } from '../../core/world';
import { RConfirm } from '../common/RConfirm';
import { useUpdateOn } from '../hook';

export const RSave = memo((props: { enableSave: boolean }) => {
  const { enableSave: haveToSave } = props;
  const [state, setState] = useState(0);

  shlog.debug(`renderer: RSave`);
  useUpdateOn(world.eventUIUpdate);

  function RSaveCard(worldData: WorldData) {
    return (
      <Card
        key={worldData.id}
        size="small"
        title={worldData.id}
        style={{ width: 400 }}
      >
        <p>
          游戏时间:{worldData.realTimeFormat} 境界:
          {globalData.level.get(worldData.player.level).name}
        </p>
        <p>
          存档于:{moment().format('lll')}({moment(worldData.realTime).fromNow()}
          )
        </p>
        <RConfirm
          title="读取存档"
          msg="读取这个存档"
          onClick={() => {
            world.saves.load(worldData);
          }}
        ></RConfirm>
        {haveToSave && (
          <Popconfirm
            title="覆盖这个存档"
            onConfirm={() => {
              world.saves.save(worldData.id);
            }}
            okText="Yes"
            cancelText="No"
          >
            <Button>覆盖存档</Button>
          </Popconfirm>
        )}
        <Popconfirm
          title="下载这个存档"
          onConfirm={() => {
            shlog.info(worldData);
            // ele可以换成打开本地文件
            shapi.downloadFile &&
              shapi.downloadFile('data.json', JSON.stringify(worldData));
          }}
          okText="Yes"
          cancelText="No"
        >
          <Button>导出存档</Button>
        </Popconfirm>
        <Popconfirm
          title="删除这个存档"
          onConfirm={() => {
            world.saves.remove(worldData.id);
          }}
          okText="Yes"
          cancelText="No"
        >
          <Button>删除存档</Button>
        </Popconfirm>
        ,
      </Card>
    );
  }

  const saves = world.saves.getSaves();

  return (
    <div>
      {saves.length === 0 ? (
        <p>当前无存档</p>
      ) : (
        saves.map((data) => {
          return RSaveCard(data);
        })
      )}

      {haveToSave && (
        <Button
          onClick={() => {
            const key = `save${new Date().getTime()}`;
            world.saves.save(key);
          }}
        >
          新的存档
        </Button>
      )}
    </div>
  );
});
