import { Tooltip } from 'antd';
import React from 'react';
import { SuperBar } from '../../core/battle/common/superBar';

const prority_color: any = ['#FF6666', '#FF9999', '#FFCCCC'];

/** 分段血条对应的展示组件 */
export function RSuperBar(props: { bar: SuperBar; height?: number }) {
  const bar = props.bar;
  const height = props.height || 45;

  let left = 0;
  const JSXItems = bar.items.map((item) => {
    const width = (item.value / bar.standard) * 100;
    const res = (
      <div
        id={item.id}
        key={item.id}
        style={{
          width: `${width}%`,
          height: `${height}px`,
          background: prority_color[item.prority],
          position: 'absolute',
          left: `${left}%`,
        }}
      >
        {width > 10 && (
          <div
            style={{
              position: 'absolute',
              width: 'auto',
              height: `${height}px`,
              right: '15px',
              color: '#ffffff',
              lineHeight: `${height}px`,
              fontSize: '16px',
            }}
          >
            {item.format()}
          </div>
        )}
      </div>
    );
    left += width;
    return res;
  });

  let container: React.CSSProperties = {
    height: `${height}px`,
    background: '#dedede',
    position: 'relative',
    overflow: 'hidden',
    borderRadius: 4,
    margin: 4,
  };
  return (
    <div
      style={container}
      onClick={() => {
        console.log(`standard:${bar.standard}`);
      }}
    >
      {JSXItems}
    </div>
  );
}
