import { RProgress } from '../common/RProgress';

export type DBMData = { title: string; release: number; total: number }[];

export function RDBM(props: { data: DBMData }) {
  const { data } = props;
  return (
    <div>
      {data.map((d) => {
        const { release, total, title } = d;
        return (
          <RProgress
            height={25}
            width={400}
            key={title}
            percent={(release / total) * 100}
            title={`${title}:${release.toFixed(1)}/${total.toFixed(1)}`}
          ></RProgress>
        );
      })}
    </div>
  );
}
