import { Battle, Buff, Entity, Skill } from '../../core/battle';
import { fisherYates, randomInt } from '../../core/utils';

creater.entity.registe('zjEnemy', (battle: Battle) => {
  const entity = new Entity(battle, {
    id: 'zjEnemy',
    skills: ['流失', '紊乱'],
  });

  let hasSkill3 = false;
  entity.eventUpdate.on(() => {
    if (battle.state.value !== 'running') {
      return;
    }
    // 筑基进度超过50%时增加衰朽技能
    let p = world.config.difficult === 'test' ? 0.1 : 0.5;
    if (battle.custom.progress >= battle.custom.progressMax * p) {
      if (!hasSkill3) {
        entity.createSkill('衰朽');
        hasSkill3 = true;
      }
    }

    // 冷却好了就用
    Object.values(entity.skills).forEach((skill) => {
      if (skill.cd > 0) {
        skill.cdRelease -= entity.battle.deltaTime;
        if (skill.cdRelease < 0) {
          skill.eventUse.emit();
        }
      }
    });
  });

  return entity;
});

creater.skill.registe('流失', (entity) => {
  const skill = new Skill(entity, {
    id: '流失',
    name: '流失',
    type: 'effect',
    desc: '每秒钟造成10伤害, 战斗时长每经过1s,伤害提升2%',
    cd: 1,
  });
  skill.eventUse.on(() => {
    const dmg = 10 * (skill.entity.battle.battleStartTime * 0.02 + 1);
    skill.custom.dmg = dmg;
    const jingmais = skill.entity.getEnemys();
    if (!jingmais.length) {
      return;
    }
    // 随机5次,造成伤害
    for (let i = 0; i < dmg; i++) {
      jingmais[randomInt(0, jingmais.length - 1)].hit(1);
    }
  });
  return skill;
});

creater.buff.registe('紊乱', (entity) => {
  return new Buff(entity, {
    id: '紊乱',
    type: 'negative',
    desc: `5秒后造成伤害`,
    release: 5,
  });
});

creater.skill.registe('紊乱', (entity) => {
  const skill = new Skill(entity, {
    id: '紊乱',
    type: 'effect',
    desc: '对三个目标施加, 6s后造成 60 伤害',
    cd: 20,
    cdRelease: 14,
  });

  let dmg = 60;
  const hitAfter = (target: Entity) => {
    const buff = target.createBuff('紊乱');
    buff.eventRemove.on(() => {
      target.hit(dmg);
    });
  };

  skill.eventUse.on(() => {
    const jingmais = skill.entity.getEnemys();
    if (!jingmais.length) {
      return;
    }
    if (jingmais.length <= 3) {
      jingmais.forEach((jm) => {
        hitAfter(jm);
      });
    } else {
      fisherYates(jingmais);
      for (let i = 0; i < 3; i++) {
        hitAfter(jingmais[i]);
      }
    }
  });
  return skill;
});

creater.buff.registe('衰朽', (entity) => {
  return new Buff(entity, {
    id: '衰朽',
    type: 'negative',
    desc: `5秒后造成伤害`,
    release: 5,
  });
});

creater.skill.registe('衰朽', (entity) => {
  const skill = new Skill(entity, {
    id: '衰朽',
    type: 'effect',
    desc: '对hp最低的目标, 6s后造成 100 伤害',
    cd: 8,
    cdRelease: 6,
  });

  let dmg = 60;
  const hitAfter = (target: Entity) => {
    const buff = target.createBuff('紊乱');
    target.inDanger = true;
    buff.eventRemove.on(() => {
      target.hit(dmg);
      target.inDanger = false;
    });
  };

  skill.eventUse.on(() => {
    const jingmais = skill.entity.getEnemys();
    if (!jingmais.length) {
      return;
    }

    const least = jingmais.sort((a, b) => {
      return a.hpbar.value - b.hpbar.value;
    })[0];

    hitAfter(least);
  });

  return skill;
});
