import {
  Battle,
  Entity,
  EntityController,
  EntityData,
} from '../../core/battle';

creater.battle.registe('元婴', (world) => {
  const battle = new Battle(world, {
    id: '元婴',
    help: `游戏说明:
  击杀心魔!`,
    player: 'yyPlayer',
    lefts: ['yyPlayer'],
    rights: ['心魔'],
  });
  return battle;
});

creater.entity.registe('yyPlayer', (battle) => {
  const data: EntityData = {
    id: 'yyPlayer',
    skills: [],
    hp: 100,
    hpmax: 100,
  };
  const entity = new Entity(battle, data);
  entity.playerController = new EntityController(entity);
  return entity;
});

creater.entity.registe('心魔', (battle) => {
  const data: EntityData = {
    id: '心魔',
    skills: [],
    hp: 10000,
    hpmax: 10000,
  };
  return new Entity(battle, data);
});
