/**
 * 灵力护盾: 引导, 最多持续30s. 引导可以持续获得护盾, 结束引导后, 获得的护盾会快速流失
 * 消耗 10/s
 * 护盾叠加速度: 20/s
 * 护盾消散速度 10%
 * 最大持续时间 30s, 可随时中断,
 * 冷却 0
 */

import {
  BarItem,
  Battle,
  Buff,
  Entity,
  EntityController,
  Skill,
} from '../../core/battle';
import { createEneityYuyao } from './yuyao';

creater.battle.registe('自定义', () => {
  const battle = new Battle(world, {
    id: '自定义',
    lefts: ['测试player'],
    player: '测试player',
    help: `
灵力护盾是一个引导技能, 引导可以快速叠盾, 停止引导会快速损失盾`,
  });

  battle.player.panel_skill['skill1_1'] = battle.player.skills['灵力护盾'];
  battle.player.panel_skill['skill1_2'] = battle.player.skills['火球术'];
  battle.player.panel_skill['skill1_3'] = battle.player.skills['火焰轰击'];
  // 敌人
  battle.addEntity('right', createEneityYuyao(battle));

  battle._mainEnemy = battle.entitys['羽妖'];

  battle.player.eventDead.on(() => {
    shlog.warn(`失败啦！`);
    battle.state.emit('fail');
  });

  battle.mainEnemy.eventDead.on(() => {
    shlog.warn(`胜利啦！`);
    battle.state.emit('success');
  });

  return battle;
});

globalData.entity.registe({
  id: '测试player',
  skills: ['灵力护盾', '火球术', '火焰轰击'],
  hp: 400,
  hpmax: 400,
  needController: true,
});

globalData.buff.registe({
  id: '灵力护盾',
  desc: '持续引导可以增加护盾, 停止引导会使护盾强度快速降低',
});

creater.skill.registe('火球术', (entity) => {
  const skill = new Skill(entity, {
    id: '火球术',
    type: 'cast',
    castCost: 2,
    icon: 'fire',
  });
  skill.eventUse.on(() => {
    skill.entity.target?.hit(600);
  });
  return skill;
});

creater.skill.registe('火焰轰击', (entity) => {
  const skill = new Skill(entity, {
    id: '火焰轰击',
    type: 'instance',
  });
  skill.eventUse.on(() => {
    skill.entity.target?.hit(100);
  });
  return skill;
});

creater.skill.registe('灵力护盾', (entity) => {
  const skill = new Skill(entity, {
    id: '灵力护盾',
    type: 'duration',
    castCost: 30,
    icon: 'magic_shield',
  });

  const battle = entity.battle;
  let buff: Buff;
  let baritem: BarItem;

  skill.eventDuration.on(() => {
    buff = entity.buffs['灵力护盾'];
    if (!buff) {
      buff = entity.addBuff('灵力护盾');
      const item = new BarItem(
        entity.hpbar,
        { id: '灵力护盾', value: 0 },
        'shield'
      );
      if (!baritem) {
        baritem = entity.hpbar.addItem(item);
      }
    }
    baritem.add(50 * battle.deltaTime);
  });
  skill.eventCancel.on(() => {
    entity.removeBuff('灵力护盾');
  });
  entity.eventUpdate.on(() => {
    if (entity.playerController && entity.playerController.casting === skill) {
      return;
    }
    if (baritem) {
      baritem.value = baritem.value * (1 - 0.5 * battle.deltaTime);
    }
  });

  return skill;
});
