declare const window: any;

export function loadElectron() {
  const electron = window.electron;
  // 如果是electron模式打开, 那么从本地文件中加载对应资源文件
  if (electron) {
    console.log('当前是electron环境, 开始加载electron数据');

    const { ipcRenderer } = electron;

    setTimeout(() => {
      ipcRenderer.send('toMain', 'hello main');
    }, 2000);

    ipcRenderer.on('toRenderer', (event: any, msg: any) => {
      console.log('收到来自main的消息:', msg);
    });
  }
}
