import { Battle, Entity, EntityController, Skill } from '../../core/battle';

/**
 * 这个思路是对的, 尽可能减少复用,能复用的部分其实都已经挪到构造里了...
 * 这样创建单个对象可以没有任何心智负担的直接调用函数即可
 * 后续扩展只要在这上面继续加level之类的参数就行了
 */
export function createEneityYuyao(battle: Battle) {
  const entity = new Entity(battle, {
    id: '羽妖',
    hp: 5000,
    hpmax: 5000,
  });

  let skill = new Skill(entity, {
    id: '普通攻击',
    type: 'cast',
    castCost: 2.5,
    desc: '普通攻击, 30伤害',
  });
  entity.addSkill(skill);
  skill.eventUse.on(() => {
    skill.target.hit(30);
  });

  skill = new Skill(entity, {
    id: '重击',
    type: 'cast',
    castCost: 5,
    cd: 8,
    cdRelease: 12,
    desc: '超强的攻击, 200伤害',
  });
  entity.addSkill(skill);
  skill.eventUse.on(() => {
    skill.target.hit(100);
  });

  entity.playerController = new EntityController(entity);

  entity.eventUpdate.on(() => {
    if (entity.battle.state.value !== 'running') {
      return;
    }
    // 虽然这样也行, 但理想来说应该给AI也配上controller
    // 不过感觉比较好的做法还是ai用ai专享controller,和player的共享部分变量
    entity.target = entity.battle.player;

    Object.values(entity.skills).forEach((skill) => {
      if (skill.cdRelease <= 0) {
        entity.playerController?.useSkill(skill);
      }
    });
  });

  return entity;
}
