/** 这里是内部mod, 其行为和mod差不多,但是打包在内部 */

function loadLocalMod(name: string) {
  require(`./${name}`);
  shlog.info(`加载本地mod:${name}`);
}

// 必须第一个加载, 其他的模块可能依赖这个模块
loadLocalMod('base');

// 由于加载顺序是原则上来说这些不许互相依赖
loadLocalMod('zjBattle');
loadLocalMod('yyBattle');
loadLocalMod('battle');
loadLocalMod('demo');

export {};
