import { Button, Tag, Tooltip } from 'antd';

export function defaultParser(value: any): string {
  switch (typeof value) {
    case 'undefined':
      return '未初始化';
    case 'number':
      return value.toLocaleString();
    case 'boolean':
      return value ? 'true' : 'false';
    case 'object':
      if (value instanceof Array) {
        return value.length.toString();
      } else {
        if (value.value) {
          return defaultParser(value.value);
        } else if (value.name) {
          return value.name;
        } else if (value.id) {
          return value.id;
        } else if (value.toString) {
          return value.toString();
        } else {
          return '未知对象';
        }
      }
    case 'function':
      return 'function';
    default:
      return value;
  }
}

/**
 * obj: 要展示的对象
 * propkey: 对象中要展示的字段, 如为空,则遍历展示所有key
 */
export function RRefLabel(props: { obj?: any; propkey?: string[] }) {
  let keys = props.propkey;
  if (!props.obj) {
    return <Tag>目标对象不存在</Tag>;
  }
  if (!keys) {
    keys = Object.keys(props.obj);
  }
  return (
    <div>
      {keys.map((key) => {
        return (
          <Tag
            key={key}
            color="blue"
            onClick={() => {
              console.log(key, props.obj[key]);
            }}
          >
            {key}: {defaultParser(props.obj[key])}
          </Tag>
        );
      })}
    </div>
  );
}

export function RTags(props: { obj: Array<any> }) {
  return (
    <div>
      {props.obj.map((key) => {
        return (
          <Tag
            key={key}
            color="blue"
            onClick={() => {
              console.log(key, key);
            }}
          >
            {defaultParser(key)}
          </Tag>
        );
      })}
    </div>
  );
}

/**
 * actions key:按钮的标题 value:按钮按下时的回调
 */
export function RActions(props: { actions: { [key: string]: () => void } }) {
  return (
    <div>
      {Object.keys(props.actions).map((key) => {
        const value = props.actions[key];
        return (
          <Button key={key} style={{ marginRight: 8 }} onClick={value}>
            {key}
          </Button>
        );
      })}
    </div>
  );
}
