import { Tabs, Button, Steps } from 'antd';
import { statisticKeys } from '../../core/world/statistics';
import { useUpdateOn } from '../hook';
import { RSave } from './RSave';
const { Step } = Steps;

export function RSide() {
  useUpdateOn(world.eventUIUpdate);
  if (world.statistics.get(statisticKeys.已完成新手引导)) {
    return <div></div>;
  } else {
    return (
      <div style={{ marginTop: 100 }}>
        <Button
          onClick={() => {
            world.statistics.add(statisticKeys.已完成新手引导);
            world.eventUIUpdate.emit();
          }}
        >
          关闭教学
        </Button>
        <Steps
          direction="vertical"
          size="small"
          current={world.statistics.get(statisticKeys.新手引导进度)}
          style={{
            width: 300,
            flexGrow: 1,
          }}
        >
          {world.statistics.newPlayerHelp.map((item) => {
            return (
              <Step
                key={item.title}
                title={item.title}
                description={item.desc}
              />
            );
          })}
        </Steps>
      </div>
    );
  }
}
