import { Popconfirm, Button } from 'antd';

export function RConfirm(props: {
  title: string;
  onClick: () => void;
  msg: string;
}) {
  const { title, onClick, msg } = props;

  if (globalSetting.enableDebug) {
    return (
      <Button
        onClick={() => {
          shlog.infoInGame(`debug: 跳过确认框: ${msg}`);
          onClick();
        }}
      >
        {title}
      </Button>
    );
  } else {
    return (
      <Popconfirm
        title={msg}
        onConfirm={() => {
          onClick();
        }}
        okText="是"
        cancelText="否"
      >
        <Button>{title}</Button>
      </Popconfirm>
    );
  }
}
