import { Button } from 'antd';
import { battleKeys } from '../../core/battle';

/** 开始欢迎页面 */
export function RBattlePrepare() {
  const dan = world.player.bag.find('筑基丹');

  if (world.nextBattle === battleKeys.筑基) {
    return (
      <div>
        <p>
          点击下方按钮开始筑基V2, 当前拥有筑基丹:
          {dan ? dan.stack : 0}个
        </p>
        <Button
          onClick={() => {
            world.startBattle(battleKeys.筑基);
            world.activeTab.value = '战斗';
          }}
        >
          开始
        </Button>
      </div>
    );
  } else if (world.nextBattle === battleKeys.元婴) {
    return (
      <div>
        <p>点击下方按钮开始突破元婴:</p>
        <Button
          onClick={() => {
            world.startBattle(battleKeys.元婴);
            world.activeTab.value = '战斗';
          }}
        >
          开始
        </Button>
      </div>
    );
  } else {
    return (
      <div>
        <p>下一场战斗:{world.nextBattle}, 尚未开发, 可以点击下面按钮直接升级</p>
        <Button
          onClick={() => {
            world.player.breakthroughSuccess();
          }}
        >
          突破
        </Button>
      </div>
    );
  }
}
