import { Button, notification, Select, Steps, Tabs, Tag } from 'antd';
import { useEmitterOn, useUpdateOn } from './hook';
import { RWorld } from './world/RWorld';
import { RZJBattle } from './battle';
import { RDebug } from './common/RDebug';
import { RSide } from './systen/RSide';
import { RYYBattle } from './battle/RYYBattle';
import { RBattle } from './battle/RBattle';

const { TabPane } = Tabs;
const { Option } = Select;
const { Step } = Steps;

const battleID_jsx: { [key: string]: JSX.Element } = {
  筑基: <RZJBattle />,
  元婴: <RYYBattle />,
  自定义: <RBattle />,
  default: <RBattle />,
};

export function RGame() {
  useUpdateOn(world.activeTab);

  function getBattle() {
    if (world.battle) {
      let userInterface = world.battle.userInterface || world.battle.id;
      console.log(userInterface);
      return battleID_jsx[userInterface];
    }
  }

  return (
    <div>
      <RDebug>
        <RHeader />
      </RDebug>

      {/* 
        尖括号引用和函数有时候不能互换; 不要搞路由
      */}
      <div style={{ display: 'flex', flexDirection: 'row' }}>
        <Tabs
          activeKey={world.activeTab.value}
          onChange={(key: any) => {
            world.activeTab.emit(key);
          }}
          tabPosition="left"
          size="small"
          style={{
            flexGrow: 2,
          }}
        >
          <TabPane tab="战斗" key="战斗" disabled={!world.battle}>
            {getBattle()}
          </TabPane>
          <TabPane tab="世界" key="世界">
            <RWorld />
          </TabPane>
        </Tabs>
        <RSide></RSide>
      </div>
    </div>
  );
}

/** 题头,展示一些全局信息 */
function RHeader() {
  console.log(world.time);
  useUpdateOn(world.eventUIUpdate);

  return (
    <div style={{ display: 'flex', flexDirection: 'row', margin: 12 }}>
      <p>
        世界时间:<Tag>{world.time.timeStr}</Tag> update耗时:{' '}
        <Tag>{world.simUsedTime.toFixed(2)}ms</Tag>
      </p>
      <div style={{ marginLeft: 16 }}>设置游戏难度:</div>
      <Select
        defaultValue={world.config.difficult}
        style={{ width: 120 }}
        onChange={(value: any) => {
          shlog.infoInGame(`难度已切换为:${value}`);
          world.config.difficult = value;
        }}
        size="small"
      >
        <Option value="test">测试(一击必杀)</Option>
        <Option value="eazy">简单</Option>
        <Option value="normal">普通</Option>
      </Select>

      <p style={{ color: 'red', marginLeft: 16 }}>
        实现功能是第一要务,没有与实际结合的架构是空中楼阁!
      </p>
    </div>
  );
}
