import { RActions, RRefLabel } from '../common/RRefLabel';
import { useEmitterOn, useUpdateOn } from '../hook';
import { REntityCard, RPlayerDetails, RSkillPanel } from '.';
import { Modal, Space, Tag } from 'antd';
import { EntityController } from '../../core/battle';
import { RDBM } from './RDBM';
import { RDebug } from '../common/RDebug';
import { RProgress } from '../common/RProgress';
import { statisticKeys } from '../../core/world/statistics';
import {
  info,
  RBattleControl,
  RBattleDebug,
  useFirstInfo,
} from './RBattleCommon';
import { ZJPlayer } from '../../extends/zjBattle/zjplayer';

export function RZJBattle() {
  const battle = world.battle;
  const player: ZJPlayer = battle.player as ZJPlayer;
  const enemy = battle.entitys['zjEnemyV2'];

  useUpdateOn(battle?.eventUpdate);
  useUpdateOn(battle?.eventSelectEntity);

  useEmitterOn(battle?.state, (res) => {
    if (res === 'success' || res === 'fail') {
      let msg = `当前战斗结果:${res}
战斗时长:${battle.battleStartTime.toFixed(2)}
使用筑基丹: ${player.筑基丹使用次数} 颗
共完成淬脉: ${player.淬脉使用次数}次, 筑基成功后血量提升
          `;
      info('战斗结束', msg, () => {
        world.activeTab.emit('世界');
        if (battle.state.value === 'success') {
          world.player.breakthroughSuccess();
        }
      });
    }
  });

  useFirstInfo(statisticKeys.已读筑基帮助);

  if (!battle) {
    return <div>未初始化battle</div>;
  }

  return (
    <div>
      <RBattleDebug />
      <RBattleControl />
      <Space></Space>
      <Tag>
        当前流失伤害:{enemy.skills['zjEnemyV2_流失']?.custom.dmg?.toFixed(0)}
      </Tag>
      <RDBM
        data={Object.values(enemy.skills).map((skill) => {
          return {
            title: skill.name,
            release: skill.cdRelease,
            total: skill.cd,
          };
        })}
      ></RDBM>
      <RProgress
        title={`筑基进度:${battle.custom.progress}/${battle.custom.progressMax}`}
        percent={(battle.custom.progress / battle.custom.progressMax) * 100}
      ></RProgress>

      <div style={{ display: 'flex', flexDirection: 'row', flexWrap: 'wrap' }}>
        {Object.values(battle.lefts).map((entity) => {
          return <REntityCard key={entity.id} entity={entity} />;
        })}
      </div>

      <RPlayerDetails
        controller={battle.player.playerController as EntityController}
      ></RPlayerDetails>
    </div>
  );
}
