import { Tag } from 'antd';
import { EntityController } from '../../core/battle';
import { RProgress } from '../common/RProgress';
import { defaultParser, RRefLabel } from '../common/RRefLabel';
import { RSkillPanel } from './RSkillPanel';

/** 角色控制面板, 包括角色施法状态, 进度条和技能面板 */
export function RPlayerDetails(props: { controller: EntityController }) {
  const { controller } = props;

  return (
    <div>
      <RProgress
        percent={controller.castingPercent}
        title={controller.castingLabel}
      ></RProgress>
      <RSkillPanel skills={controller.entity.panel_skill}></RSkillPanel>
    </div>
  );
}
