import { Row, Col, Slider, Button } from 'antd';
import { useState } from 'react';
import { RConfirm } from '../common/RConfirm';
import { RBag } from './RBag';

export function RShop() {
  const [value, setValue] = useState(30);
  const onChange = (value: any) => {
    setValue(value);
  };
  const player = world.player;
  return (
    <div>
      <RBag></RBag>
      <Button
        onClick={() => {
          player.bag.money -= 10000;
          player.bag.addItem('筑基丹');
          world.eventUIUpdate.emit();
        }}
      >
        购买筑基丹, 10000灵石
      </Button>
    </div>
  );
}
