import { Entity } from '.';
import { CommonData, CommonObject, PluginRegister } from '../system';
import { SHEvent, EventCanUse } from '../utils';

export interface SkillData extends CommonData {
  /**
   * cast 施法, 读条(急速),进入公cd, 施法结束后生效
   * instance 施法,进入公cd(急速),立即生效
   * duration 引导, 持续时间固定, 每隔一定间隔(受急速影响)生效一次.
   * effet 无条件立即生效
   */
  type: 'cast' | 'instance' | 'duration' | 'effect';

  castCost?: number;
  /** type=duration,隔多长时间生效一次 */
  effectInterval?: number;
  /** 使用次数限制, -1为无限次, 0不可用 */
  useLimit?: number;

  cd?: number;
  cdRelease?: number;

  icon?: string;
}

export class Skill extends CommonObject<SkillData> {
  type: 'cast' | 'instance' | 'duration' | 'effect';

  /** 释放需要读条的时间,0表示瞬发 */
  castCost: number;
  /** 剩余可用次数 -1表示无限制 */
  useLimit: number;
  /** 冷却 */
  cd: number;
  /** 冷却剩余 */
  cdRelease: number;
  /** 使用的图标 */
  icon: string;
  /** 技能所属的目标 */
  entity: Entity;

  /** 技能一定会有一个目标， 即使是非指向性，有一个目标也不是坏事。。。 */
  target: Entity;

  eventUse: SHEvent = new SHEvent();
  eventCanUse: EventCanUse = new EventCanUse();
  /** 引导期间持续回调, 需要一段时间回调一次的, 自己保存变量去.. */
  eventDuration: SHEvent = new SHEvent();
  /** 取消施法 */
  eventCancel: SHEvent = new SHEvent();

  custom: any = {};

  /** 用于UI展示的当前技能数据 */
  public get iconData() {
    let desc = this.name;
    let percent = 100;
    let center = undefined;
    if (this.cd > 0) {
      if (this.cdRelease <= 0) {
        percent = 100;
      } else {
        center = this.cdRelease.toFixed(1) + 's';
        percent = ((this.cd - this.cdRelease) / this.cd) * 100;
      }
    }

    const control = this.entity.playerController;
    if (control) {
      if (control.commonCDRelease > 0 && percent === 100) {
        percent = (1 - control.commonCDRelease / control.commonCD) * 100;
      }
    }

    if (this.useLimit === 0) {
      percent = 0;
    } else if (this.useLimit > 0) {
      center = this.useLimit.toString();
    }

    return { desc, percent, center };
  }

  constructor(entity: Entity, data: SkillData) {
    super(data);
    this.entity = entity;

    this.type = data.type;
    this.castCost = data.castCost || 0;
    this.useLimit = data.useLimit || -1;
    this.cd = data.cd || 0;
    this.cdRelease = data.cdRelease || 0;
    this.icon = data.icon || 'default';
    /** 默认指向目标为自己 */
    this.target = entity;

    this.eventCanUse.on(() => {
      if (this.cdRelease > 0) {
        shlog.warn(`技能 [${this.id}] 冷却未结束`);
        return false;
      }
      if (this.useLimit === 0) {
        shlog.warnInGame(`技能 [${this.id}] 可用次数用光了`);
        return false;
      }
      return true;
    });

    this.eventUse.on(() => {
      this.cdRelease = this.cd;
      if (this.useLimit >= 0) {
        this.useLimit--;
      }
    });
  }
}
