import { World } from '.';
import { SHEvent } from '../utils';

export const statisticKeys = {
  新手引导进度: '新手引导进度',
  已完成新手引导: '已完成新手引导',
  闭关修炼次数: '闭关修炼次数',
  突破次数: '突破次数',
  获得物品: '获得物品',
  已读筑基帮助: '已读筑基帮助',
  已读元婴帮助: '已读元婴帮助',
};

export class Statistics {
  _data: { [key: string]: number };
  world: World;
  constructor(world: World) {
    this.world = world;
    this._data = {};
    this.eventAdd.on(() => {
      this.checkStep();
    });
  }

  eventAdd: SHEvent = new SHEvent();

  get(id: string) {
    if (!this._data[id]) {
      this._data[id] = 0;
    }
    return this._data[id];
  }

  add(id: string, add?: number) {
    if (!this._data[id]) {
      this._data[id] = 0;
    }
    let a = add || 1;
    this._data[id] += a;
    this.eventAdd.emit();
  }

  checkStep() {
    if (this.get(statisticKeys.已完成新手引导)) {
      return;
    }

    const step = this.get(statisticKeys.新手引导进度);
    const stepCtx = this.newPlayerHelp[step];
    if (!stepCtx) {
      return;
    }
    const toNext = stepCtx.check();
    shlog.log(`当前进度:${step}, 完成进度检查结果:${toNext}`);
    if (toNext) {
      this.add(statisticKeys.新手引导进度);
      this.checkStep();
    }
  }

  newPlayerHelp = [
    {
      title: '完成一次修炼',
      desc: '在城市中可以修炼,进入[修炼]页面,完成一次修炼',
      check: () => {
        return this.get(statisticKeys.闭关修炼次数);
      },
    },
    {
      title: '突破至练气中期',
      desc: '当修为满了后,点击[突破],完成一次突破',
      check: () => {
        return this.world.player.level > 1;
      },
    },
    {
      title: '获得一颗筑基丹',
      desc: '筑基丹是筑基的重要道具,去[灵药堂]可以购买筑基丹,测试中钱可以无限花',
      check: () => {
        const zjd = this.world.player.bag.find('筑基丹');
        return zjd && zjd.stack > 0;
      },
    },
    {
      title: '突破至筑基期',
      desc: '突破筑基需要经过特殊战斗,点击[突破],突破到筑基期',
      check: () => {
        return this.world.player.level > 3;
      },
    },
    {
      title: '已完成',
      desc: '恭喜已完成引导, 点击关闭可以关掉这个窗口',
      check: () => {
        return true;
      },
    },
  ];
}
