import { World, WorldData } from '.';
import { GlobalSetting } from '../bootstrap';
import { Player } from './player';
import { SHTime } from './time';

export class WorldSave {
  world: World;
  constructor(world: World) {
    this.world = world;
  }

  getSaves(): WorldData[] {
    shlog.log(`当前storage状态:`, shapi.getStorageKeys());

    const saves: WorldData[] = shapi
      .getStorageKeys()
      .map((key) => {
        if (key.slice(0, 4) === 'save') {
          return JSON.parse(shapi.getStorage(key) as string);
        }
        return null;
      })
      .filter((r) => r !== null);

    return saves;
  }

  load(data: WorldData) {
    if (data.version !== globalData.VERSION) {
      shlog.warnInGame('当前存档版本和游戏版本不一致, 存档可能损坏');
    }
    try {
      this.world.time = new SHTime(this.world, data.time);
      this.world.player = new Player(this.world, data.player);
      this.world.config = data.config;
      this.world.statistics._data = data.statistics;
      this.world.characters['player'] = this.world.player;
    } catch (e) {
      shlog.warnInGame('存档已损坏', e);
      return;
    }

    shlog.infoInGame('读取成功', data);
    this.world.state.emit('pause');
  }

  save(id: string) {
    const save: WorldData = {
      id,
      time: this.world.time.time,
      realTimeFormat: this.world.time.timeStr,
      player: this.world.player.save(),
      realTime: new Date().getTime(),
      config: this.world.config,
      version: globalData.VERSION,
      statistics: this.world.statistics._data,
    };
    shlog.infoInGame('保存成功', save);
    shapi.setStorage(id, save);
    this.world.eventUIUpdate.emit();
    return save;
  }

  remove(id: string) {
    shapi.removeStorage(id);
    shlog.infoInGame('删除成功');
    world.eventUIUpdate.emit();
  }
}

export function saveSetting() {
  const save: GlobalSetting = globalSetting;
  shapi.setStorage('globalSetting', save);
}
export function loadSetting(): GlobalSetting {
  const setting = shapi.getStorage('globalSetting');
  if (setting) {
    return JSON.parse(setting);
  } else {
    return {
      enableDebug: false,
      showCommonCD: true,
    };
  }
}
