import { Entity } from '.';
import { CommonData, CommonObject, PluginRegister } from '../system';
import { SHEvent } from '../utils';

export type BuffType = 'normal' | 'positive' | 'negative';

export interface BuffData extends CommonData {
  type?: BuffType;
  release?: number;
  releaseMax?: number;
  stackMax?: number;
}

export class Buff extends CommonObject<BuffData> {
  type: BuffType;
  // 负数为无限制时间
  release: number;
  releaseMax: number;
  entity: Entity;

  eventRemove: SHEvent = new SHEvent();

  stack: number = 0;
  stackMax: number;

  constructor(entity: Entity, data: BuffData) {
    super(data);
    this.entity = entity;
    this.type = data.type || 'normal';
    this.release = data.release || -1;
    this.releaseMax = data.releaseMax || -1;
    this.stackMax = data.stackMax || 1;

    if (this.release >= 0) {
      const handleUpdate = () => {
        this.release -= this.entity.battle.deltaTime;
        if (this.release < 0) {
          this.eventRemove.emit();
          this.entity.removeBuff(this.id);
          this.entity.eventUpdate.off(handleUpdate);
        }
      };
      this.entity.eventUpdate.on(handleUpdate);
    }
  }

  public get iconData() {
    let str = this.name;
    if (this.release > 0) {
      str += `${this.release.toFixed(1)}s`;
    }
    if (this.stack > 1) {
      str += `[${this.stack}]`;
    }
    return str;
  }
}
