/*
  例如护盾符
  使用时为本体套一个护盾buff, 
  buff在血条处进行了一个对象注册, 如果被打碎了, 那么回调buff,销毁buff

  引导护盾
  持续引导,增加引导buff, 引导中断buff会直接消失
  buff会在血条处注册一个位置, 被打碎了并没啥影响..

  气疗术
  读条,给自己回点血
*/

import { SHEvent } from '../../utils';

export type BarType = 'basic' | 'shield';

export const type_prority = {
  basic: 0,
  shield: 1,
};

/**
 * 多节进度条系统
 * 其中由很多个小节组成, 最后拼成一个大进度条
 * 消耗会优先消耗右面的小节, 消耗存在百分比,例如护盾只削减50%的伤害,
 * 同级别的,谁后来的谁在右面, 不同级别的,高级别的永远在右边
 * 级别0 为本体血条和法力
 */
export class SuperBar {
  // 由于有严格顺序要求, 用数组, 数组内容发生改变后重新排序
  items: BarItem[] = [];
  // 标准长度代表的值, 每个item的长度取决于与这个值的相对值
  standard: number = 100;

  eventBasicEmpty: SHEvent = new SHEvent();
  eventChange: SHEvent = new SHEvent();

  basic: BarItem;

  constructor(params: IBarItem) {
    this.basic = new BarItem(this, params, 'basic');
    this.items.push(this.basic);

    let limitMax = params.maxValue ? params.maxValue : params.value;
    this.standard = limitMax;

    this.eventChange.on(() => {
      let total = this.totalValue;
      if (total < limitMax) {
        this.standard = limitMax;
      } else {
        this.standard = total;
      }
    });
  }

  public get value() {
    return this.basic.value;
  }

  public get maxValue() {
    return this.basic.maxValue;
  }

  public get totalValue() {
    let sum = 0;
    this.items.forEach((item) => {
      sum += item.value;
    });
    return sum;
  }

  minus(value: number) {
    // 依次扣减, 直到value全部消化
    for (let i = this.items.length - 1; i >= 0; i--) {
      const item = this.items[i];
      value = item.minus(value);
      if (value === 0) {
        break;
      }
    }
    // 移除需要移除的项目
    this.items = this.items.filter((i) => {
      if (i.value <= 0) {
        if (i.type === 'basic') {
          this.eventBasicEmpty.emit();
        }
        // 当某个条的值已经空了， 触发事件
        i.eventEmpty.emit();
        // 如果需要自动移除，则直接移除
        if (i.removeOnEmpty) {
          i.eventRemove.emit();
          return false;
        }
      }
      return true;
    });
  }

  addItem(item: BarItem) {
    this.items.push(item);
    this.items.sort((a, b) => {
      return a.prority - b.prority;
    });
    return item;
  }

  // 手动移除,不要与自动移除同时使用
  removeItem(item: BarItem) {
    this.items = this.items.filter((i) => {
      return i.id !== item.id;
    });
  }
}

export interface IBarItem {
  id: string;
  value: number;
  maxValue?: number;
  removeOnEmpty?: boolean;
}

export class BarItem implements IBarItem {
  bar: SuperBar;
  id: string;

  format: () => string;

  type: string;
  // 优先级
  prority: number;
  // 剩余值
  _value: number = 0;
  /** 最大值, 有的同种护盾有最大值,暂不开发 */
  maxValue: number;

  /** 拦截伤害比例, 默认1, 暂不开发 */
  defense: number = 1;

  /** 当value为0时是否自动移除 */
  removeOnEmpty: boolean;

  eventEmpty: SHEvent = new SHEvent();
  eventRemove: SHEvent = new SHEvent();
  eventOverMax: SHEvent = new SHEvent();

  constructor(bar: SuperBar, params: IBarItem, type: BarType) {
    this.bar = bar;
    this.id = params.id;

    this.type = type;
    if (type_prority[type] === undefined) {
      shlog.error(`superbar: 未知的type:${type}`);
    }

    this.removeOnEmpty = false;
    this.prority = type_prority[type];
    this._value = params.value;
    this.maxValue = params.maxValue || -1;

    this.format = () => {
      if (this.maxValue > 0) {
        return `${this.value.toFixed(0)}/${this.maxValue?.toFixed(0)}`;
      } else {
        return this.value.toFixed(0);
      }
    };
  }

  public set value(v) {
    this._value = v;
    this.bar.eventChange.emit();
  }
  public get value() {
    return this._value;
  }

  add(add: number) {
    this.value += add;
    if (this.maxValue > 0 && this.maxValue && this.value > this.maxValue) {
      this.value = this.maxValue;
      this.eventOverMax.emit();
    }
    this.bar.eventChange.emit();
  }

  // 减去指定值， 如果够用，则返回0，如果条已经没了，则返回剩余的值
  minus(hit: number) {
    if (this.value > hit) {
      this.value -= hit;
      this.bar.eventChange.emit();
      return 0;
    } else {
      hit -= this.value;
      this.value = 0;
      this.bar.eventChange.emit();
      return hit;
    }
  }
}
