import { SHEvent } from '../utils';

export class InputManager {
  /** 游戏标准按键, 第一排技能 */
  skillFirstLine = [
    'skill1_1',
    'skill1_2',
    'skill1_3',
    'skill1_4',
    'skill1_5',
    'skill1_6',
  ];

  /** 游戏标准按键, 第二排技能 */
  skillSecondLine = [
    'skill2_1',
    'skill2_2',
    'skill2_3',
    'skill2_4',
    'skill2_5',
    'skill2_6',
  ];

  /** 键盘keycode 映射到游戏标准按键 */
  keyboard_key: { [key: string]: string } = {
    '1': 'skill1_1',
    '2': 'skill1_2',
    '3': 'skill1_3',
    '4': 'skill1_4',
    '5': 'skill1_5',
    '6': 'skill1_6',
    q: 'skill2_1',
    w: 'skill2_2',
    e: 'skill2_3',
    r: 'skill2_4',
    t: 'skill2_5',
    y: 'skill2_6',
    ' ': 'interrupt',
  };

  /** 游戏标准按键 映射到键盘keycode, 与上面的map保持同步 */
  key_keyboard: { [key: string]: string } = {};

  eventKey: SHEvent<string> = new SHEvent();
  constructor() {
    Object.keys(this.keyboard_key).forEach((key) => {
      const value = this.keyboard_key[key];
      this.key_keyboard[value] = key;
    });

    shapi.eventKeyUp((key: string) => {
      if (this.keyboard_key[key]) {
        this.eventKey.emit(this.keyboard_key[key]);
        shlog.info(`按下按键:[${key}]-[${this.keyboard_key[key]}]`);
      } else {
        shlog.warn(`按下按键:[${key}]-[未找到注册按键]`, key);
      }
    });
  }
}
