import { CommonData, CommonObject, PluginRegister } from '../system';
import { SHEvent, SHRef } from '../utils';
import { World } from '../world';
import { EntityController } from './common/controller';
import { Entity, EntityData, EntitySide } from './entity';

export type BattleKey = '筑基' | '金丹' | '元婴' | '自定义';

export const battleKeys = {
  筑基: '筑基',
  金丹: '金丹',
  元婴: '元婴',
  自定义: '自定义',
};

/**
 * 初始 prepare, 改为running后开始更新, pause时暂停, success或fail也暂停
 */
export type BattleState = 'prepare' | 'running' | 'paused' | 'success' | 'fail';

export interface BattleData extends CommonData {
  help?: string;
  player?: string;
  lefts?: string[];
  rights?: string[];
  others?: string[];
  /** 使用的UI界面, 默认使用default */
  userInterface?: string;
}

export class Battle extends CommonObject<BattleData> {
  world: World;
  interval: any;
  deltaTime: number;

  battleStartTime: number;

  _player?: Entity;
  public get player(): Entity {
    if (this._player) {
      return this._player;
    } else {
      return this.entitys['player'];
    }
  }
  _mainEnemy?: Entity;
  public get mainEnemy(): Entity {
    if (this._mainEnemy) {
      return this._mainEnemy;
    } else {
      return this.entitys['enemy'];
    }
  }

  entitys: { [key: string]: Entity } = {};

  /** 添加一个entity */
  addEntity(side: EntitySide, entity: Entity) {
    this.entitys[entity.id] = entity;
    entity.side = side;
    if (entity.side === 'left') {
      this.lefts.push(entity);
    } else if (entity.side === 'right') {
      this.rights.push(entity);
    }
    return entity;
  }

  /** 通过id,从creater里创建entity */
  createEntity(id: string, side: EntitySide): Entity {
    let entity = creater.entity.create(id, this);
    return this.addEntity(side, entity);
  }

  /** 位于左面的单位 */
  lefts: Entity[] = [];
  /** 位于右面的单位 */
  rights: Entity[] = [];

  help: string;

  // prepare 或 running状态下每帧触发
  eventUpdate: SHEvent = new SHEvent();
  eventPerSecond: SHEvent = new SHEvent();
  eventSelectEntity: SHEvent<string> = new SHEvent();

  state: SHRef<BattleState> = new SHRef<BattleState>('prepare');

  realTimeInSecend: number = 0;

  userInterface: string;
  constructor(world: World, data: BattleData) {
    super(data);
    this.userInterface = data.userInterface || 'default';
    this.world = world;
    this.help = data.help || 'help';

    // 生命周期
    this.interval = undefined;
    this.deltaTime = 0.02;

    if (data.lefts) {
      data.lefts.forEach((id) => {
        this.createEntity(id, 'left');
      });
    }
    if (data.rights) {
      data.rights.forEach((id) => {
        this.createEntity(id, 'right');
      });
    }
    if (data.others) {
      data.others.forEach((id) => {
        this.createEntity(id, 'others');
      });
    }
    if (data.player) {
      this._player = this.entitys[data.player];
    }

    this.state.on((state) => {
      if (state === 'running') {
        this.startUpdate();
      } else if (
        state === 'paused' ||
        state === 'success' ||
        state === 'fail'
      ) {
        clearInterval(this.interval);
        this.interval = undefined;
      }
    });

    this.battleStartTime = 0;

    let second = 0;
    let realTimeBegin = 0;
    this.eventUpdate.on(() => {
      if (this.state.value === 'running') {
        this.battleStartTime += this.deltaTime;
        second += this.deltaTime;
        if (second > 1) {
          second = 0;
          this.eventPerSecond.emit();
          const realtimeNow = new Date().getTime();
          this.realTimeInSecend = realtimeNow - realTimeBegin;
          realTimeBegin = realtimeNow;
        }
      }
    });

    this.startUpdate();
  }

  init(): void {
    // 监听改变玩家指向的事件
    this.eventSelectEntity.on((id) => {
      if (!this.entitys[id].canSelect) {
        return;
      }
      this.player.target = this.entitys[id];
      Object.values(this.entitys).forEach((e) => {
        if (e.id === id) {
          e.isSelect = true;
        } else {
          e.isSelect = false;
        }
      });
    });

    // 当玩家释放技能时,触发一次战斗开始
    this.player.playerController?.eventUseSkill.once(() => {
      this.state.emit('running');
    });
  }

  simUsedTime: number = 0;

  startUpdate() {
    if (this.interval) {
      // prepare 也会启动interval, prepare切换至running的时候也会触发这个事件...
      return;
    }
    this.interval = setInterval(() => {
      if (this.state.value === 'running' || this.state.value === 'prepare') {
        let start = new Date().getTime();
        this.eventUpdate.emit();
        this.simUsedTime = new Date().getTime() - start;
      }
    }, this.deltaTime * 1000);
  }
}
