import { World } from '.';

export class SHTime {
  /** 当前世界时间 */
  time: number;
  world: World;

  year: number = 0;
  moon: number = 0;
  day: number = 0;

  constructor(world: World, time?: number) {
    this.time = time || 0;
    this.world = world;
    if (this.time) {
      const { year, moon, day } = this.parse(this.time);
      this.year = year;
      this.moon = moon;
      this.day = day;
    }
  }

  add(value: number) {
    this.time += value;
    const { year, moon, day } = this.parse(this.time);
    if (this.year !== year) {
      this.world.eventUpdateYear.emit(year);
    }
    this.year = year;
    if (this.moon !== moon) {
      this.world.eventUpdateMoon.emit(moon);
    }
    this.moon = moon;
    this.day = day;
  }

  parse(number: number) {
    let _time = Math.floor(number);
    let year = Math.floor(_time / 360);
    _time = _time - year * 360;
    let moon = Math.floor(_time / 30);
    _time = _time - moon * 30;
    let day = _time;
    return {
      year,
      moon,
      day,
      timStr: `${year}年${moon}月${day}日`,
    };
  }

  public get timeStr() {
    return `${this.year}年${this.moon}月${this.day}日`;
  }
}
