import { PluginRegister } from '.';

export interface CommonData {
  id: string;
  name?: string;
  desc?: string;
  custom?: any;
}

export abstract class CommonObject<T extends CommonData> {
  id: string;
  _name?: string;
  /** 描述 */
  desc: string;
  data: T;
  custom?: any;
  public get name() {
    return this._name ? this._name : this.id;
  }

  /** load方法通常使用构造创建 */
  constructor(data: T) {
    this.id = data.id;
    this._name = data.name;
    this.data = data;
    this.desc = data.desc || '无描述';
    this.custom = data.custom;
  }

  /** 有一个save方法,  */
  save(): T {
    throw new Error('当前对象没有save()实现');
  }
}
