import { Row, Col, Slider } from 'antd';
import { useState } from 'react';
import { RConfirm } from '../common/RConfirm';

export function RCultivate() {
  const [value, setValue] = useState(30);
  const onChange = (value: any) => {
    setValue(value);
  };
  const player = world.player;
  return (
    <div>
      <p>当前境界修炼速度: {player.levelData.cultivateSpeed}</p>
      <p>天赋加成: {player.cultivateSpeedByTalent}</p>
      <p>总速度: {player.cultivateSpeed}</p>

      <Row>
        <Col span={12}>
          <Slider
            min={1}
            max={10000}
            onChange={onChange}
            value={typeof value === 'number' ? value : 0}
          />
        </Col>
        <Col span={3}>
          <div>{world.time.parse(value).timStr}</div>
        </Col>
      </Row>
      <RConfirm
        title="开始修炼"
        msg={`开始修炼, 预计修炼:${world.time.parse(value).timStr}, 获得修为:${
          value * player.cultivateSpeed
        }`}
        onClick={() => {
          player.cultivate(value);
        }}
      ></RConfirm>
      {player.canBreakthrough ? (
        <div>
          <p> 当前可以突破到下一等级[{player.nextLevelName}] </p>
          <RConfirm
            title="开始突破"
            msg={`点击后突破到下一级${player.nextLevelName}`}
            onClick={() => {
              player.breakthrough();
            }}
          ></RConfirm>
        </div>
      ) : (
        <p> 当前经验不足以突破到下一等级</p>
      )}
    </div>
  );
}
